import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";

const PrivacyFixedBottom = () => {
  return (
    <div className="fixed-bottom bg-dark text-white px-4 py-4">
      <div className="row align-items-center d-flex justify-content-between">
        <div className="col-md-10 mb-2 mb-md-0">
          <h5>We value your privacy</h5>
          <p className="mb-0">
            This site collects your data in order to improve your experience in
            the form of cookies. We use this data to provide social media
            features, personalize content and analyze our traffic. By clicking
            "Accept" below you consent to the use of this technology.
          </p>
        </div>
        <div className="col-md-2 d-flex flex-row flex-md-column justify-content-md-end gap-2 ">
          <button
            className="btn btn-primary"
            style={{
              width: "120px",
              whiteSpace: "nowrap",
              float: "right",
              padding: "8px 13px",
            }}
          >
            Accept All
          </button>
          <button
            className="btn btn-danger"
            style={{
              width: "120px",
              whiteSpace: "nowrap",
              padding: "8px 13px",
            }}
          >
            Reject All
          </button>
        </div>
      </div>
    </div>
  );
};

export default PrivacyFixedBottom;
